.right-bar-container{
  width: 112px;
  right: 0px;
  background-color: white;
  max-height: 100vh;
  height: calc(100vh - 80px);
  border-left: 2px solid #f2f4f9;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  padding-top: 4.8rem;
  padding-bottom: 4.8rem;
}

.right-bar-container__widget-button{
  height: 4.8rem;
  width: 4.8rem;
}

.right-bar-container__widget-button__picture{
  height: 4.8rem;
  width: 4.8rem;
}



